.input-players{
    display:flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 30px;
    input{
        text-align: center;
        margin-bottom: 20px;
        height: 50px;
        font-family: 'Josefin Sans', 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
      font-size: 1.5rem;
      border-radius: 10px;
      border-style: none;
    }
    label{
        text-align: center;
        margin-bottom: 10px;
        font-size: 1.4rem;
    }
}

.player-table{
    height: 200px;
    width: 80vw;
    @media (min-width: 37.5em) {
        width: 500px; 
    }
    text-align: start;
    overflow: auto;
    scrollbar-base-color: $primary-colour;
    scrollbar-color: $sec-colour;
    th{
        text-align: start;
    }
}